.FileHeader {
    position: absolute;
    z-index: 1;
    height: 80px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    max-width: 100%;
    overflow: hidden;
}

.FileHeader__BackContainer {
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.File__MainContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    position: relative;
}

.File__ImageContainer {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    margin-bottom: 3em;
}

.Prev:hover > .Overlay, .Next:hover > .Overlay {
    display: -ms-flexbox;
    display: flex;
}

.FileOverlay__Link {
    position: absolute;
    top: 0;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;

    color: #fff;
    text-decoration: none;
}

.FileOverlay__Link--Prev {
    left: 0;
    width: 30%;
    -ms-flex-align: start;
        align-items: flex-start;
    padding-left: 30px;
}

.FileOverlay__Link--Next {
    right: 0;
    width: 45%;
    -ms-flex-align: end;
        align-items: flex-end;
    padding-right: 30px;
}

.FileOverlay__ChevronContainer {
    background: rgba(0,0,0,.5);
    display: none;
    font-size: 60px;
    padding: 20px;
    border-radius: 5px;
}

.FileOverlay__Link:hover > .FileOverlay__ChevronContainer {
    display: -ms-flexbox;
    display: flex;
    max-width: 100%;
}

.File__Image {
    display: inline;
    max-width: 100%;
    max-height: 100%;
}

.Table {
    margin: 20px 0;
    width: 100%;
    max-width: 100%;
    border-spacing: 0;
    border-collapse: collapse;
}

.Table th, .Table td {
    padding: 8px;
    line-height: 1.42857143;
    vertical-align: top;
    border-top: 1px solid #ddd;
}
.Table th {
    text-align: left;
}



